import { Input, Form } from 'antd'
import { useParams } from 'react-router'
import baseApi from 'service/baseApi'
import Button from 'components/common/Buttons'
import CommentBox from 'components/common/CommentBox'
import CommentItem from 'components/detail/CommentItem'

const DetailComments = ({ comments, setComments }) => {
  const { gameNo } = useParams()
  const [form] = Form.useForm()

  // ëŒ“ê¸€ë°œí–‰
  const onFinish = async (values) => {
    try {
      await baseApi.post(`/games/${gameNo}/comments`, {
        reviewContents: values.comments,
      })
      form.resetFields()
      const res = await baseApi.get(`/games/${gameNo}/comments`)
      setComments(res.data)
    } catch (error) {
      console.log(error)
      alert('ë¡œê·¸ì¸ í›„ ì´ìš©ê°€ëŠ¥í•©ë‹ˆë‹¤.')
    }
  }

  return (
    <>
      {comments.content.map(
        (comment) =>
          comment && (
            <CommentItem
              comment={comment}
              key={comment.commentNo}
              setComments={setComments}
            />
          )
      )}

      <Form form={form} onFinish={onFinish}>
        <CommentBox TextBox>
          <Form.Item
            name="comments"
            rules={[
              {
                required: true,
                message: 'ëŒ“ê¸€ì„ ìž…ë ¥í•˜ì„¸ìš”',
              },
            ]}
          >
            <Input.TextArea
              placeholder="ëŒ“ê¸€ì„ ìž…ë ¥í•˜ì„¸ìš”"
              className="CommentBox"
            />
          </Form.Item>
          <Form.Item style={{ alignSelf: 'self-end' }}>
            <Button type="submit">ëŒ“ê¸€ë‹¬ê¸°</Button>
          </Form.Item>
        </CommentBox>
      </Form>
    </>
  )
}

export default DetailComments
