import React, { useContext } from 'react'
import baseApi from '../../service/baseApi'
import { UserContext } from '../../service/authState'
import ReviewList from './ReviewList'
import { customIcons, historyType } from 'components/common/constants'
import { Rate } from 'antd'
import styled from 'styled-components'
import Flexbox from 'components/common/Flexbox'
import AvatarBase from 'components/common/AvatarBase'
import DefaultImg from 'components/common/images/img-user-default.png'
import Button from 'components/common/Buttons'

const PopUpProfile = ({ applyUser }) => {
  const { user } = useContext(UserContext)

  if (!user) return <></>
  const uid = user.uid

  console.log(applyUser)
  // console.log(gameUser)
  // ì¹œêµ¬ ì¶”ê°€
  const addFriend = (e) => {
    console.log('addfriendí˜¸ì¶œ')
    baseApi
      .post('/users/me/friends', {
        addedUserUid: uid,
      })
      .then(async (response) => {
        const res = await response.data.content
        console.log(res)
        alert('ì¹œêµ¬ê°€ ì¶”ê°€ë˜ì—ˆìŠµë‹ˆë‹¤.')
        e.currentTarget.disabled = true
        // setFriends(res)
      })
      .catch((error) => {
        console.log(error)
        alert('ì¹œêµ¬ì¶”ê°€ì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.')
      })
  }

  console.log(user)
  return (
    <PopUpSection>
      {/* <Profile className="profile" /> */}

      <div>
        {user && (
          <Flexbox>
            <AvatarBase
              style={{
                flexDirection: 'column',
                padding: '40px',
                border: '1px solid lightGray',
                borderRadius: '4px',
                position: 'sticky',
                top: 0,
              }}
            >
              <a
                href="#!"
                className="avatarImg"
                style={{ height: '80px', width: '80px' }}
              >
                <img src={DefaultImg} alt={DefaultImg} />
              </a>
              <a
                href="#!"
                className="nickname"
                style={{
                  fontSize: '16px',
                  fontWeight: '700',
                  display: 'block',
                  padding: '10px 0 5px 0',
                }}
              >
                <strong>{applyUser.gameUser.nickname}</strong>
              </a>
              <Rate
                disabled
                defaultValue={4}
                character={({ index }) => customIcons[index + 1]}
              />
              <p
                className="info"
                style={{ fontSize: '12px', padding: '10px 0' }}
              >
                <span>
                  {applyUser.gameUser.locCd.locSdName}{' '}
                  {applyUser.gameUser.locCd.locSkkName}
                </span>
                <span>{historyType[applyUser.gameUser.history]}</span>
              </p>
              <Button
                Secondary
                height={'25px'}
                width={'90px'}
                style={{ fontSize: '12px', fontWeight: '400' }}
                onClick={(e) => addFriend()}
              >
                ì¹œêµ¬ ì¶”ê°€
              </Button>
            </AvatarBase>
          </Flexbox>
        )}
      </div>
      <ReviewList className="reviewList" />
    </PopUpSection>
  )
}

export default PopUpProfile

const PopUpSection = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  .profile {
    flex: 1 1 25%;
  }
  .reviewList {
    flex: 1 1 65%;
    padding-left: 2rem;
    margin-left: 2rem;
  }
`
