import React, { useEffect } from 'react'

const { kakao } = window

const MapContainer = ({
  courts,
  searchPlace,
  setCourtInfo,
  onAddressChange,
}) => {
  console.log('ì§€ë„:', searchPlace)

  // kakao map ë¶ˆëŸ¬ì˜¤ê¸°
  useEffect(() => {
    const container = document.getElementById('myMap')
    const options = {
      center: new kakao.maps.LatLng(33.450701, 126.570667),
      level: 3,
    }
    const map = new kakao.maps.Map(container, options)

    // ìž¥ì†Œê²€ìƒ‰ ê°œì²´ ì„¤ì •
    const ps = new kakao.maps.services.Places()

    // í‚¤ì›Œë“œë¡œ ìž¥ì†Œê²€ìƒ‰
    ps.keywordSearch(searchPlace, placesSearchCB)

    // í‚¤ì›Œë“œ ê²€ìƒ‰ ì™„ë£Œì‹œ í˜¸ì¶œë˜ëŠ” ì½œë°±í•¨ìˆ˜
    function placesSearchCB(data, status, pagination) {
      if (status === kakao.maps.services.Status.OK) {
        // ê²€ìƒ‰ëœ ìž¥ì†Œ ìœ„ì¹˜ë¥¼ ê¸°ì¤€ìœ¼ë¡œ ì§€ë„ ë²”ìœ„ë¥¼ ìž¬ì„¤ì •í•˜ê¸°ìœ„í•´ LatLngBiunds ê°ì²´ì— ì¢Œí‘œë¥¼ ì¶”ê°€
        let bounds = new kakao.maps.LatLngBounds()

        displayMarker()

        for (let i = 0; i < data.length; i++) {
          bounds.extend(new kakao.maps.LatLng(data[i].y, data[i].x))
        }
        // ê²€ìƒ‰ëœ ìž¥ì†Œ ìœ„ì¹˜ë¥¼ ê¸°ì¤€ìœ¼ë¡œ ì§€ë„ ë²”ìœ„ë¥¼ ìž¬ì„¤ì •
        map.setBounds(bounds)
      }
    }

    // ì§€ë„ì— ë§ˆì»¤ë¥¼ í‘œì‹œí•˜ëŠ” í•¨ìˆ˜
    function displayMarker() {
      courts.content.map((court) => {
        var markerPosition = new kakao.maps.LatLng(court.lat, court.lon)
        var marker = new kakao.maps.Marker({ map, position: markerPosition })

        // ë§ˆì»¤ í´ë¦­ ì´ë²¤íŠ¸
        kakao.maps.event.addListener(marker, 'click', function () {
          setCourtInfo(court)
          onAddressChange(court)
          console.log('ì§€ë„ì“°', court)
        })
      })
    }
  }, [searchPlace])

  return (
    <div>
      <div id="myMap" style={{ width: '100%', height: '500px' }}></div>
    </div>
  )
}

export default MapContainer
