import React, { useState, useEffect, useContext } from 'react'
import { useHistory } from 'react-router'
import { UserContext } from '../../service/authState'
import baseApi from '../../service/baseApi'

import Banner from './Banner'
import Searching from './Searching'
import GameCard from './GameCard'
import RecomList from 'components/listPage/RecomList'

import styled from 'styled-components'
import { Pagination, Affix, Grid, Tag } from 'antd'
import Loading from 'components/common/Loading'

const ListPage = () => {
  const { user } = useContext(UserContext)
  const { useBreakpoint } = Grid
  const screens = useBreakpoint()
  const history = useHistory()

  const uid = user && user.uid
  const [games, setGames] = useState(null)
  const [loading, setLoading] = useState(true)
  const [recommends, setRecommends] = useState(null)
  const [loadingFri, setLoadingFri] = useState(true)
  const [locSds, setLocSds] = React.useState(null)
  const [locSkks, setLocSkks] = React.useState(null)
  const [courtData, setCourtData] = React.useState([])
  const [courts, setCourts] = React.useState([courtData][0].courtNo)
  const [genderType, setGenderType] = React.useState([])
  const [historyType, setHistoryType] = React.useState([])
  const [ageType, setAgeType] = React.useState([])

  const [totalPage, setTotalPage] = useState(0)
  const [minIndex, setMinIndex] = useState(0)
  const [maxIndex, setMaxIndex] = useState(0)
  const [current, setCurrent] = useState(0)

  const pageSize = 6

  // ê²Œìž„ë¦¬ìŠ¤íŠ¸ ë¶ˆëŸ¬ì˜¤ê¸°
  useEffect(() => {
    setLoading(true)
    baseApi
      .get('/games') //
      .then((response) => {
        // console.log(response.data.content)
        setLoading(false)
        setGames(response.data.content)
        setTotalPage(response.data.content.length / pageSize)
        setMinIndex(0)
        setMaxIndex(pageSize)
      })
  }, [])

  const onGameClick = (game) => {
    history.push(`/pages/${game.gameNo}/detail`)
  }

  //==================================================
  // ì‹œë„ êµ°êµ¬ì— ë”°ë¥¸ ì½”ë“œìž¥ ì´ë¦„ë“¤ ë¶ˆëŸ¬ì˜¤ê¸°
  useEffect(() => {
    baseApi
      .get('/courts', {
        params: {
          locSd: locSds,
          locSkk: locSkks,
        },
      })
      .then(function (response) {
        setCourtData(response.data.content)
      }, [])
      .catch((error) => {
        console.log(error)
      })
  }, [locSkks])

  useEffect(() => {
    setCourtData(courtData)
  }, [courtData])

  //--------------------------------------------------------------
  const handleLocSdChange = (value) => {
    setLocSds(value)
  }

  const handleLocSkkChange = (value) => {
    setLocSkks(value)
    // console.log('locSkks2', locSkks)
  }
  const handleCourtChange = (courtno) => {
    setCourts(courtno)
    // console.log('setcourts1', courts)
  }

  const handleGenderChange = (value) => {
    setGenderType(value)
  }

  const handleHistoryChange = (value) => {
    setHistoryType(value)
  }

  const handleAgeChange = (value) => {
    setAgeType(value)
  }

  // ======================================================
  // ê²€ìƒ‰í•˜ê¸°
  const handleSearch = (values) => {
    // form.resetFields()
    setGames(null)
    setLoading(true)

    baseApi
      .get('/games', {
        params: {
          courtNo: values.courtNo,
          genderType: values.genderType,
          historyType: values.historyType,
          ageType: values.ageType,
          locSd: values.locSd,
          locSkk: values.locSkk,
        },
      })
      .then(async (response) => {
        const res = await response.data.content
        console.log(res)

        if (res) {
          // console.log('gamesres', res)
          setTotalPage(res.length / pageSize)
          setMinIndex(0)
          setMaxIndex(pageSize)
          setLoading(false)
          setGames(res)
        } else if (!res) {
          alert('ê²€ìƒ‰ê²°ê³¼ê°€ ì—†ìŠµë‹ˆë‹¤')
        }
      })
      .catch((error) => {
        console.log(error)
        alert('ê²€ìƒ‰ê²°ê³¼ê°€ ì—†ìŠµë‹ˆë‹¤')
        setLoading(false)
        setGames(null)
      })
  }

  useEffect(() => {
    setLoading(false)
  }, [games])

  // ======================================================
  // ì¹œêµ¬ì¶”ì²œë¦¬ìŠ¤íŠ¸
  useEffect(() => {
    console.log('ì¶”ì²œì¹œêµ¬ë¦¬ìŠ¤íŠ¸')
    baseApi
      .get('/users/me/friends/recommend', {
        uid: uid,
      })
      .then(async (response) => {
        const res = await response.data.content
        // console.log('recommend', res)
        setLoadingFri(false)
        setRecommends(res)
      })
      .catch((error) => {
        console.log(error)
      })
  }, [])

  const handleChange = (page) => {
    setCurrent(page)
    setMinIndex((page - 1) * pageSize)
    setMaxIndex(page * pageSize)
  }

  return (
    <>
      <Banner />

      <ScreenWrap>
        <FloatBanner className="recommendDiv">
          <Affix offsetTop={120}>
            <RecomList recommends={recommends} loadingFri={loadingFri} />
          </Affix>
        </FloatBanner>
        <Section>
          <div className="searchDiv">
            <h3 id="searchA" className="title">
              ê²€ìƒ‰í•˜ê¸°
            </h3>
            <Searching
              locSds={locSds}
              locSkks={locSkks}
              courtData={courtData}
              courts={courts}
              handleLocSdChange={handleLocSdChange}
              handleLocSkkChange={handleLocSkkChange}
              handleCourtChange={handleCourtChange}
              onFinish={handleSearch}
              handleGenderChange={handleGenderChange}
              handleHistoryChange={handleHistoryChange}
              handleAgeChange={handleAgeChange}
              genderType={genderType}
              historyType={historyType}
              ageType={ageType}
            />
          </div>
          <div className="gamesDiv">
            <h3 className="title">í˜„ìž¬ ê°€ëŠ¥í•œ ê²½ê¸°</h3>
            {loading ? (
              <Loading />
            ) : (
              <ul className="gamesList">
                {games ? (
                  games.map((game) => (
                    <GameCard //
                      key={game.gameNo}
                      game={game}
                      onGameClick={onGameClick}
                    />
                  ))
                ) : (
                  <div className="resultDiv">
                    <h1>
                      ê²€ìƒ‰ê²°ê³¼ê°€ ì—†ìŠµë‹ˆë‹¤{' '}
                      <img
                        src="/images/img-tennis-ball.png"
                        alt="ball"
                        width="20em"
                      />{' '}
                    </h1>
                  </div>
                )}
              </ul>
            )}
          </div>
        </Section>
        <div className="page">
          {games && (
            <Pagination
              pageSize={pageSize}
              current={1}
              total={games.length}
              onChange={handleChange}
            />
          )}
        </div>
      </ScreenWrap>
    </>
  )
}

export default ListPage

const ScreenWrap = styled.div`
  @media screen and (max-width: 376px) {
    flex-direction: column;
    .recommendDiv {
    }
    .ant-affix {
      position: static;
    }
    .searchDiv,
    .gamesDiv {
      flex: 1 100%;
    }
  }
  position: relative;
  width: 100vw;
  .page {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
  }
`
const Section = styled.div`
  max-width: 1200px;
  display: flex;
  flex-direction: row;
  justify-content: center;

  @media screen and (max-width: 1200px) {
    .searchDiv {
      width: 100%;
      margin-left: 10%;
    }
    .gamesDiv {
      margin-left: 10%;
      margin-top: 20%;
      .resultDiv {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        line-height: 20em;
      }
    }
  }
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  padding-top: 50px;
  padding-bottom: 10%;
  margin: auto;
  .title {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 2rem;
  }
  .searchDiv {
    flex: 1 23%;
  }
  .gamesDiv {
    flex: 1 77%;
    .spin {
      color: #78ca1e;
    }
    .listDiv {
      display: flex;
    }
    .gamesList {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
    }
  }
`

const FloatBanner = styled.div`
  position: absolute;
  top: 0;
  right: 20px;
`
