import React, { useState, useEffect, useContext } from 'react'
import { UserContext } from 'service/authState'
import { useParams, useHistory } from 'react-router'
import styled from 'styled-components'
import { Row, Col, Spin } from 'antd'
import { DownOutlined, UpOutlined } from '@ant-design/icons'
import baseApi from 'service/baseApi'
import { antIcon } from 'components/common/constants'
import DetailTable from './DetailTable'
import DetailComments from './DetailComments'
import Avatar from 'components/common/Avatar'
import Button from 'components/common/Buttons'

const DetailMain = () => {
  const { user } = useContext(UserContext)
  const history = useHistory()
  const { gameNo } = useParams()
  const [game, setGame] = useState(null)
  const [comments, setComments] = useState(null)
  const [commentsVisible, setCommentsVisible] = useState(false)
  const [applys, setApplys] = useState(null)
  const [loading, setLoading] = useState(false)

  useEffect(() => {
    fetchData()
  }, [])

  const fetchData = async () => {
    setLoading(true)
    try {
      const games = await baseApi(`/games/${gameNo}`)
      setGame(games.data)

      const history = await baseApi(`games/histories/applygames`) //
      setApplys(history.data.content)
      setLoading(false)

      const comment = await baseApi(`/games/${gameNo}/comments`)
      setComments(comment.data)
    } catch (error) {
      console.log(error)
    }
  }

  if (applys !== null && game !== null) {
    var result = applys.find((e) => e.joinedGame.gameNo === game.gameNo)
    var today = new Date()
    var endDt = new Date(game.endDt)
    var lastDay = new Date(endDt.setHours(endDt.getHours() + 15))
  }

  // ê²Œìž„ì‹ ì²­ ë²„íŠ¼í´ë¦­
  const gameApply = async () => {
    if (window.confirm('ì‹ ì²­ í•˜ì‹œê² ìŠµë‹ˆê¹Œ?')) {
      try {
        const apply = await baseApi.post(`/games/${gameNo}/apply`)
        if (apply.data) {
          alert('ì‹ ì²­ì´ ì™„ë£Œë˜ì—ˆìŠµë‹ˆë‹¤')
          const applygames = await baseApi(`games/histories/applygames`) //

          setApplys(applygames.data)
        }
      } catch (error) {
        console.log(error)
        alert('ì‹ ì²­ì´ ë¶ˆê°€ëŠ¥í•©ë‹ˆë‹¤')
      } finally {
        fetchData()
      }
    }
  }

  // ê¸€ìˆ˜ì •
  const edit = () => {
    history.push(`/pages/${gameNo}/editing`)
  }

  // ê¸€ì‚­ì œ
  const del = async () => {
    if (window.confirm('ì‚­ì œ í•˜ì‹œê² ìŠµë‹ˆê¹Œ?')) {
      try {
        const del = await baseApi.delete(`/games/${gameNo}`)
        if (del.data) {
          alert('ë°œí–‰ê¸€ì´ ì‚­ì œë˜ì—ˆìŠµë‹ˆë‹¤')
          history.push('/')
        }
      } catch (error) {
        console.log(error)
      }
    }
  }

  const showComment = () => {
    setCommentsVisible(!commentsVisible)
  }

  return (
    <div>
      <Row>
        <Col xs={{ span: 20, offset: 2 }} lg={{ span: 12, offset: 5 }}>
          {game && (
            <div key={game.gameNo}>
              <TitleWrap>
                <h1>{game.title}</h1>
              </TitleWrap>
              <Avatar game={game} />
              {loading ? (
                <Flexbox style={{ height: '100vh' }}>
                  <Spin indicator={antIcon} />
                </Flexbox>
              ) : (
                <DetailTable game={game} />
              )}
              {user &&
                (user.uid === game.gameCreator.uid ? (
                  <Flexbox>
                    <Button
                      height={'40px'}
                      onClick={edit}
                      style={{ marginRight: '5px' }}
                    >
                      ìˆ˜ì •
                    </Button>
                    <Button height={'40px'} onClick={del}>
                      ì‚­ì œ
                    </Button>
                  </Flexbox>
                ) : (
                  <Flexbox>
                    {(game !== null &&
                      result !== undefined &&
                      result.joinedGame.gameNo === game.gameNo) ||
                    today > lastDay ? (
                      <Button
                        Primary
                        height={'40px'}
                        width={'200px'}
                        style={{ pointerEvents: 'none' }}
                      >
                        {today > lastDay ? 'ì‹ ì²­ë§ˆê°' : 'ì‹ ì²­ì™„ë£Œ'}
                      </Button>
                    ) : (
                      <Button
                        Outlined
                        height={'40px'}
                        width={'200px'}
                        onClick={gameApply}
                      >
                        ì‹ ì²­í•˜ê¸°
                      </Button>
                    )}
                  </Flexbox>
                ))}
            </div>
          )}

          {comments && (
            <p
              style={{
                cursor: 'pointer',
                fontWeight: 'bold',
                margin: '60px 0',
              }}
              onClick={showComment}
            >
              ëŒ“ê¸€{' '}
              {comments.totalElements === 0 ? null : comments.totalElements}
              {commentsVisible ? (
                <UpOutlined
                  style={{
                    fontSize: '14px',
                    marginLeft: '5px',
                    paddingBottom: '5px',
                  }}
                />
              ) : (
                <DownOutlined
                  style={{
                    fontSize: '14px',
                    marginLeft: '5px',
                    paddingBottom: '5px',
                  }}
                />
              )}
            </p>
          )}

          {commentsVisible && (
            <DetailComments comments={comments} setComments={setComments} />
          )}
        </Col>
      </Row>
    </div>
  )
}
const Flexbox = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
`
const TitleWrap = styled.div`
  padding: 32px 48px 32px 0;

  h1 {
    font-size: 48px;
    font-weight: bold;
  }
`
export default DetailMain
