import { Grid, Form } from 'antd'
import React, { useState } from 'react'
import { useHistory } from 'react-router'
import baseApi from 'service/baseApi'
import Write from './Write'

const { useBreakpoint } = Grid
const Writing = () => {
  const history = useHistory()
  const [form] = Form.useForm()
  const [courtInfo, setCourtInfo] = useState('')
  const [isModalVisible, setIsModalVisible] = useState(false)
  var screens = useBreakpoint()
  console.log(screens)

  // map containerì—ì„œ ì§€ë„ì •ë³´ ê°€ì ¸ì˜¤ê¸°
  const onAddressChange = (value) => {
    form.setFieldsValue({
      court: `${value.name}`,
      courtNo: ` ${value.courtNo}`,
    })
    setIsModalVisible(false)
  }

  // ë°œí–‰í•˜ê¸°

  const onFinish = async (values) => {
    try {
      const post = await baseApi.post('/games', {
        title: values.title,
        genderType: values.genderType,
        historyType: Number(values.historyType),
        ageType: Number(values.ageType),
        strDt: values.strDt,
        endDt: values.endDt,
        content: values.content,
        courtNo: values.courtNo,
      })

      alert('ë°œí–‰ì´ ì™„ë£Œë˜ì—ˆìŠµë‹ˆë‹¤')
      history.push(`/pages/detail/${post.data.gameNo}`)
    } catch (error) {
      console.log(error)
    }
  }

  return (
    <div>
      <Write
        setCourtInfo={setCourtInfo}
        courtInfo={courtInfo}
        onAddressChange={onAddressChange}
        isModalVisible={isModalVisible}
        setIsModalVisible={setIsModalVisible}
        form={form}
        onFinish={onFinish}
        courtInfo={courtInfo}
      />
    </div>
  )
}

export default Writing
